import React, { useState, useEffect, useContext } from 'react';
import { useNavigate } from "react-router-dom";
import axios from 'axios';
import ValidateEmail from './validation/EmailValid';
import AnimateError from './validation/AnimateError';
import ValidateMobile from './validation/ValidateMobile';
import cities from './CitiesList';

import { UserContext } from "../App";

export default function Register() {
    let navigate = useNavigate();
    const {state, dispatch} = useContext(UserContext);
    const [srvErr, setSrvErr] = useState([]);
    const [disable, setDisable] = useState(false);
    const [username, setUsername] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [confPass, setConfPass] = useState('');
    const [mobile, setMobile] = useState('');
    const [city, setCity] = useState('');
    const [errors, setErrors] = useState({
        username: '',
        email: '',
        password: '',
        confPass: '',
        mobile: '',
        city: ''
    });
    useEffect(() => {
        if(state) {
            navigate("/");
        }
    }, []);
    const regSubmit = (e) => {
        e.preventDefault();
        let errorCount = 0;
        if(username === '') {
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, username: 'Name is Required'}
            });
            AnimateError();
        } else if(username.length < 2 || !isNaN(username)){
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, username: 'Invalid name'}
            });
            AnimateError();
        } else {
            setErrors((prevState)=> {
                return {...prevState, username: ''}
            });
        } if(email === '') {
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, email: 'Email is Required'}
            });
            AnimateError();
        } else if(ValidateEmail(email)){
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, email: 'Email ID not valid'}
            });
            AnimateError();
        } else {
            setErrors((prevState)=> {
                return {...prevState, email: ''}
            });
        } if(password === '') {
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, password: 'Password is Required'}
            });
            AnimateError();
        } else if(password.length < 5) {
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, password: 'Password must have 6 charecters'}
            });
            AnimateError();
        } else {
            setErrors((prevState)=> {
                return {...prevState, password: ''}
            });
        } if(confPass === '') {
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, confPass: 'Enter Confirm Password'}
            });
            AnimateError();
        } else if(confPass !== password) {
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, confPass: 'Password not match'}
            });
            AnimateError();
        } else {
            setErrors((prevState)=> {
                return {...prevState, confPass: ''}
            });
        } if(mobile === '') {
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, mobile: 'Mobile Number Required'}
            });
            AnimateError();
        } else if(ValidateMobile(mobile)){
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, mobile: 'Mobile Number is not valid'}
            });
            AnimateError();
        } else {
            setErrors((prevState)=> {
                return {...prevState, mobile: ''}
            });
        } if(city === '') {
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, city: 'City Required'}
            });
            AnimateError();
        } else {
            setErrors((prevState)=> {
                return {...prevState, city: ''}
            });
        }
        if(errorCount === 0) {
            setSrvErr("");
            setDisable(true);
            let regData = {name: username, email, password, mobile, city}
            axios.post('api/register', regData)
            .then(function (response) {
                localStorage.setItem("token", "Bearer " + response.data.token);
                navigate("/dashboard");
            })
            .catch(function (error) {
                if(error.response.data[0].msg === "Email you have entered is already exists") {
                    errorCount++;
                    setErrors((prevState)=> {
                        return {...prevState, email: 'Email you have entered is already exists'}
                    });
                    AnimateError();
                    setDisable(false);
                } else {
                    setSrvErr(error.response.data);
                    setDisable(false);
                }
            });
        }
    }
    const [citystr, setCitystr] = useState('');
    const [cityshow, setCityshow] = useState(false);
    const citySelect = (e) => {
        document.getElementById('city').value = e.target.getAttribute("name");
        setCityshow(false);
        setCity(e.target.getAttribute("name"));
    }

    const cityString = (e) => {
        if(e.target.value.length > 2) {
            setCityshow(true);
        } else {
            setCityshow(false);
        }
        setCitystr(e.target.value);
        setCity(e.target.value);
    }

    const filterCity = cities.filter((element) => {
        return element.toLowerCase().match(citystr.toLowerCase());
    });
    return (
        <>
            <form className="auth_form contact-form" autoComplete="off" onSubmit={regSubmit}>
                <div className="row">
                    <div className="col-sm-12">
                        <div className="form_wr_input">
                            <input className="form_input_style" value={username} onChange={(e)=>setUsername(e.target.value)} type="text" placeholder="Full Name" />
                            <span className="form_err">{errors.username}</span>
                        </div>
                    </div>
                    <div className="col-sm-12">
                        <div className="form_wr_input">
                            <input className="form_input_style" value={email} onChange={(e)=>setEmail(e.target.value)} type="text" placeholder="E-mail" />
                            <span className="form_err">{errors.email}</span>
                        </div>
                    </div>
                    <div className="col-sm-12">
                        <div className="form_wr_input">
                            <input className="form_input_style" value={password} onChange={(e)=>setPassword(e.target.value)} type="password" placeholder="Password" />
                            <span className="form_err">{errors.password}</span>
                        </div>
                    </div>
                    <div className="col-sm-12">
                        <div className="form_wr_input">
                            <input className="form_input_style" type="password" value={confPass} onChange={(e)=>setConfPass(e.target.value)} placeholder="Confirm Password" />
                            <span className="form_err">{errors.confPass}</span>
                        </div>
                    </div>
                    <div className="col-sm-12">
                        <div className="form_wr_input">
                            <input className="form_input_style" type="tel" value={mobile} onChange={(e)=>setMobile(e.target.value)} placeholder="10 Digit Phone Number" />
                            <span className="form_err">{errors.mobile}</span>
                        </div>
                    </div>
                    <div className="col-sm-12">
                        <div className="form_wr_input relative">
                            <input className="form_input_style" type="text" id="city" value={city} onChange={cityString} placeholder="City" />
                            {(cityshow && filterCity.length !== 0) ? <div className="page_city_wrap">
                                {filterCity.map((filtcities) => <span className="page_city_list" key={filtcities} name={filtcities} onClick={citySelect}>{filtcities}</span>)}
                            </div> : ""}
                            <span className="form_err">{errors.city}</span>
                        </div>
                        <button className="site-btn sb-big" disabled={disable}>Login</button>
                    </div>
                    <div className="col-sm-12 server_error">
                        {srvErr && srvErr.map(err =>
                            <div key={err.msg} className="form_err">{err.msg}</div>
                        )}
                    </div>
                </div>
                {/* <div className="either_quote"><span>OR</span></div>
                <div className="social_media_wrap">
                    <img src="/img/fb_login.jpg" alt="login fb" />
                    <div className="ht_x">&nbsp;</div>
                    <img src="/img/gg_login.jpg" alt="login fb" />
                </div> */}
            </form>
        </>
    )
}